body {
  @media screen and (min-width: 250px) and (max-width: 768px) {

    // header
    .header {
      margin: 0px;
      font-family: 'montserrat', sans-serif;
      background: #fff;
      padding: 15px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;

      .logo {
        width: 48px;
      }

      .libertee {
        display: flex;
        flex-direction: column;
        align-items: center;

        .libertee-logo {
          width: 98px;
          height: 26px;
        }

        p {
          font-size: 9px;
          font-weight: 300;
          align-items: center;
        }
      }

      .navigation-icons {
        font-size: 18px;
        ;
      }

    }

    // navigation
    .navigation {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-content: space-between;
      align-items: center;
      margin: 30px;
      font-size: 14px;
      border-bottom: 1px solid #fff;

      span {
        display: flex;
        flex-direction: row;
        align-items: center;
        padding: 5px;
        cursor: pointer;
      }
    }
  }

